
import store from '@/Store';
import { Options, Vue } from 'vue-class-component';
import Menu, { Item } from '@/components/Menu.vue'; // @ is an alias to /src
import Notification from '@/components/Notification.vue';
import notification from '@/Notification';
import {
    startLog,
} from '@/Geoloc';

/* Texts */
const textStart = 'Démarrer la randonée';
const textSettings = 'Configuration';

@Options({
    components: {
        Menu,
        Notification,
    },
})
export default class Home extends Vue {
    get settingsList() {
        const list: Item[] = [];

        list.push({
            label: textStart,
            id: 'start',
            disabled: this.isRecording,
        });

        return list;
    }

    get menuList() {
        const list: Item[] = [];

        list.push({
            label: textSettings,
            id: 'settings',
        });

        return list;
    }

    get isRecording() {
        return store.geolocSettings.isRecording;
    }

    get list() {
        const openMenu = store.openMenu;
        const settingsList = this.settingsList;
        const menuList = this.menuList;

        if (openMenu === 'settings') {
            return settingsList;
        } else if (openMenu === 'menu') {
            return menuList;
        }
        return [] as Item[];
    }

    buttonSettings() {
        store.openMenu = 'settings';
    }

    buttonActions() {
        console.log('TODO button actions');
        notification('TODO button actions', {
        });
    }

    buttonMenu() {
        store.openMenu = 'menu';
    }

    menuSelection(id: string) {
        switch(id) {
            case 'start':
                startLog();
                break;
            case 'settings':
                notification('Not implemented yet', {type: 'error'});
                break;
        }
    }
}
